import React, { Component } from 'react';
import { BrowserRouter as Router, Route, NavLink,Switch } from 'react-router-dom';
import Home from './../pages/Home';
import PositionList from './../pages/PositionList';
import './index.less';

class App extends Component {
  render() {
    return (<Router>
      <div className="app">
        <div className="app-header">
          <div className="header-main">
            <a className="app-logo">
              <img src="https://img.alicdn.com/tfs/TB1Zv8_lxSYBuNjSspjXXX73VXa-390-63.png" height="20"/>
            </a>
            <span className="app-title"></span>
            <ul className="app-menu">
              <li>
                <NavLink to="/" exact activeClassName="app-menu-active">
                  &nbsp;&nbsp;
                </NavLink>
              </li>
              <li>
                <NavLink to="/positionList" activeClassName="app-menu-active">
                  
                </NavLink>
              </li>
              <li>
                <NavLink to="/intro" activeClassName="app-menu-active"></NavLink>
              </li>
              <li>
                <NavLink to="/intro" activeClassName="app-menu-active"></NavLink>
              </li>
              <li>
                <NavLink to="/intro" activeClassName="app-menu-active"></NavLink>
              </li>
            </ul>
            <div className="login">
              
              <span>
                <a></a>&nbsp;|&nbsp;
                <a></a>
              </span>
            </div>
          </div>
        </div>
        <div className="content">
          <Switch>
            <Route exact path="/" component={Home}/>
            <Route path="/positionList" component={PositionList}/>
          </Switch>
        </div>
      </div>
    </Router>
    );
  }
}

export default App;



